html {
  font-family: Roboto, sans-serif;
}

:root {
  --color: #00acdc;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-size: 12px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo_png {
  width: 40px;
  height: 40px;
  display: block;
}

.nav_list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  text-transform: uppercase;
  text-align: center;
  font-size: 12px;
  font-weight: 500;
}

.nav_item:not(:last-child) {
  margin-right: 20px;
}

.nav_link {
  color: #000;
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: flex;
}

.nav_link:hover {
  color: var(--color);
}

.is-active {
  color: var(--color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
